import { Component, OnInit } from '@angular/core';
import { AgentService } from '../../shared/services/agent';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-add-customer',
  templateUrl: './add-customer.component.html',
  styleUrls: ['./add-customer.component.css']
})
export class AddCustomerComponent implements OnInit {

  addCustomer ={}
  constructor(private agentService:AgentService, private toastrService: ToastrService) { }

  ngOnInit() {
  }

  Agent =[
    {value:0, name: "Deal done"},
    {value:1, name: "Introduction calls"},
    {value:2, name: "Follow up calls"},
   //{value:3, name: "Deal done"},
    {value:3, name: "Callback"},
    {value:4, name: "Voicemail"},
    {value:5, name: "Blacklist"},
    {value:6, name: "Number does not match"},
    {value:7, name: "Underage"},
    {value:8, name: "Duplicate"},
  ]

  SuccessRate =[
    {value: 0, name: "low"} ,
    {value: 1, name: "medium"},
    {value: 2, name: "high"} //low medium high
  ]

  RegistrationStatus =[
    {value: 0, name: "lead"} ,
    {value: 1, name: "demo"} ,
    {value: 2, name: "real"} 
  ]

  submit(){
    //alert('submit')
    console.log(this.addCustomer);
    this.agentService.addCustomerByAgent(this.addCustomer).subscribe((data)=>{
      console.log(data);
      this.showToaster();
      this.addCustomer ={}
    })
  }

    //notification
    showToaster(){
      this.toastrService.success('Data saved Successfully');
    }
    ErrorSuccess(){
      this.toastrService.error('unsuccessful');
    }
    infoSuccess(){
      this.toastrService.info('info msg','Info!');
    }
    warningSuccess(){
      this.toastrService.warning('this is warning');
    }
}
